<template>
  <div id="app">
    <div class="top-bar">
      <div class="top-bar-buttons">
        <a @click="loginModal = true" v-show="!isLogin">登录</a>
        <a v-if="!isLogin">注册</a>
        <a v-show="isLogin" @click="logout">退出登录</a>
        <a>消息通知</a>
        <a>
          <router-link to="/Pay">
            <img class="button-label" src="./assets/icons/cart.png"/>购物车
          </router-link>
        </a>
      </div>
    </div>
    <div class="top-nav-bar">
      <Row type="flex" justify="space-around" align="middle" class="nav-bar-row">
        <Col span="18" class="nav-bar-buttons">
        <div class="logo"></div>
        <router-link to="/">首页</router-link>
        <router-link to="/hotel">住宿</router-link>
        <router-link to="/Agricultural">农副产品</router-link>
        </Col>
      </Row>
    </div>
    <Modal
      v-model="loginModal"
      title="登录">
      <Form ref="formInline" :model="formInline" :rules="ruleInline">
        <FormItem prop="user">
          <Input type="text" v-model="formInline.user" placeholder="Username">
          <Icon type="ios-person-outline" slot="prepend"></Icon>
          </Input>
        </FormItem>
        <FormItem prop="password">
          <Input type="password" v-model="formInline.password" placeholder="Password">
          <Icon type="ios-locked-outline" slot="prepend"></Icon>
          </Input>
        </FormItem>
      </Form>
      <div slot="footer">
        <Button type="success" size="large" long :loading="loginLoading" @click="handleSubmit('formInline')">登录</Button>
      </div>
    </Modal>
    <router-view></router-view>
    <div class="footer">2016-2017 © 麻城纯阳山</div>
  </div>
</template>

<script>
export default {
  name: 'app',
  data () {
    return {
      isLogin: false,
      loginModal: false,
      loginLoading: false,
      formInline: {
        user: '',
        password: ''
      },
      ruleInline: {
        user: [
          { required: true, message: '请输入账号名称', trigger: 'blur' }
        ],
        password: [
          { required: true, message: '请填写登录密码', trigger: 'blur' },
          { type: 'string', min: 6, message: '密码长度至少为6位', trigger: 'blur' }
        ]
      }
    }
  },
  methods: {
    handleSubmit (name) {
      this.$refs[name].validate((valid) => {
        if (valid && this.formInline.user === 'test' && this.formInline.password === '123456789') {
          this.$Message.success('登陆成功')
          this.loginModal = false
          this.isLogin = true
        } else {
          this.$Message.error('登录失败')
        }
      })
    },
    logout () {
      this.isLogin = false
      this.$Message.success('退出登陆成功')
    }
  }
}
</script>

<style scoped>
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #f8f8f9;
}

  .logo {
    width: 90px;
    height: 90px;
    background-color: orange;
  }
  .top-bar {
    height: 40px;
    display: flex;
    flex-direction: row;
    justify-content: flex-end;
    align-items: center;
    background-color: aliceblue;
  }

  .top-bar-buttons {
    color: white;
    margin-right: 50px;
    font-size: 16px;
  }
.top-bar-buttons a {
  margin-left: 10px;
}
  .button-label {
    margin-right: 5px;
  }
  .top-nav-bar {
    border-bottom: solid lightgrey 1px;
    height: 70px;
  }

  .top-nav-bar a {
    font-size: 20px;
  }

  .nav-bar-row {
    height: 100px;
    background-color: white;
  }

  .nav-bar-buttons {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
  }
  .nav-bar-buttons a {
    margin-left: 40px;
    font-size: 20px;
  }
  .footer {
    height: 100px;
    padding-top: 25px;
    color: lightgrey;
    margin-left: auto;
    margin-right: auto;
    text-align: center;
  }
</style>



