import Vue from 'vue'
import Router from 'vue-router'
import Hello from '@/components/Hello'
import Hotel from '@/components/Hotel'
import HotelMess from '@/components/HotelMess'
import Agricultural from '@/components/Agricultural'
import Article from '@/components/Article'
import Pay from '@/components/Pay'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Hello',
      component: Hello
    },
    {
      path: '/hotel',
      name: 'Hotel',
      component: Hotel
    },
    {
      path: '/HotelMess',
      name: 'HotelMess',
      component: HotelMess
    },
    {
      path: '/Agricultural',
      name: 'Agricultural',
      component: Agricultural
    },
    {
      path: '/Article',
      name: 'Article',
      component: Article
    },
    {
      path: '/Pay',
      name: 'Pay',
      component: Pay
    }
  ]
})



