<template>
    <div class='pay'>
      <div class="main-content">
        <div class="products">
          <Card class="pro-card" v-for=" item in products" v-bind:key="item.productID">
            <a slot="extra" @click="removePro(item)">
              <Icon type="close-round" color="red"></Icon>
            </a>
            <div class="product">
              <div class="products-pic"></div>
              <div class="buy-info">
                <p class="pro-name">{{item.productName}}</p>
                <p class="product-number-des">
                  <span class="pro-number">{{item.productPricePer}}</span><span class="unit">{{item.productUnit}}</span>
                </p>
                <p class="pro-total-price">
                  共计<span>{{item.productPricePer * item.productNumber}}</span>元
                </p>
                <div class="number-change">
                  <Button type="primary" shape="circle" icon="plus-round" @click="add(item)"></Button>
                  <span>{{item.productNumber}}</span>
                  <Button type="primary" shape="circle" icon="minus-round" @click="minusNumber(item)"></Button>
                </div>
              </div>
            </div>
          </Card>
        </div>
        <div class="pay-for-it">
          <div class="total-price-number">
            <p>共计<span class="price-number">{{totalPrice}}</span>元</p>
          </div>
          <Button type="success" class="pay-button" size="large">下单支付</Button>
        </div>
      </div>
    </div>
</template>

<script>
  import _ from 'lodash'
  export default {
    name: 'pay',
    data () {
      return {
        products: [
          {
            productID: 1,
            productName: '白菜苔',
            productPricePer: 18,
            productUnit: '斤',
            productNumber: 8
          },
          {
            productID: 2,
            productName: '白菜苔',
            productPricePer: 32,
            productUnit: '斤',
            productNumber: 6
          },
          {
            productID: 3,
            productName: '白菜苔',
            productPricePer: 25,
            productUnit: '斤',
            productNumber: 3
          },
          {
            productID: 4,
            productName: '白菜苔',
            productPricePer: 55,
            productUnit: '斤',
            productNumber: 2
          },
          {
            productID: 5,
            productName: '白菜苔',
            productPricePer: 18,
            productUnit: '斤',
            productNumber: 8
          }
        ]
      }
    },
    methods: {
      add (product) {
        product.productNumber ++
      },
      minusNumber (product) {
        if (product.productNumber > 1) {
          product.productNumber --
        } else {
          product.productNumber --
          _.remove(this.products, product)
        }
      },
      removePro (product) {
        product.productNumber --
        _.remove(this.products, product)
      }
    },
    computed: {
      totalPrice: function () {
        let that = this
        let result = 0
        for (let number = 0; number < that.products.length; number++) {
          result += that.products[number].productPricePer * that.products[number].productNumber
        }
        return result
      }
    }
  }
</script>

<style scoped>
  .main-content {
    width: 800px;
    min-height: 800px;
    margin-left: auto;
    margin-right: auto;
    margin-top: 50px;
  }
  .products {
    width: 100%;
  }
  .pro-card {
    height: 90px;
  }
  .product {
    width: 100%;
    height: 70px;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-content: center;
  }
  .products-pic {
    height: 70px;
    width: 100px;
    background-color: orange;
  }
  .buy-info {
    width:500px;
    display: flex;
    flex-direction: row;
    align-content: center;
    justify-content: space-between;
    line-height: 70px;
  }
  .pro-name {
    font-size: 20px;
  }
  .product-number-des {
    font-size: 20px;
  }
  .pro-number {
    color: red;
  }
  .number-change {
    font-size: 20px;
  }
  .pro-total-price {
    font-size: 20px;
  }
  .pro-total-price span {
    color: red;
  }
  .pay-for-it {
    width: 100%;
    padding-right: 20px;
    padding-top: 20px;
    display: flex;
    flex-direction: row;
    justify-content: flex-end;
    align-content: flex-end;
  }
  .pay-button {
    float: right;
  }
  .total-price-number {
    line-height: 50px;
    margin-right: 30px;
    font-size: 20px;
  }

  .price-number {
    color: red;
  }
</style>



