<template>
    <div class='hotelMess'>
      <div class="product-mess">
        <div class="left-product-pic"></div>
        <div class="right-product-info">
          <p class="product-name">阳光大床房</p>
          <p class="product-price"><span class="price-number">350</span>/晚</p>
          <Rate v-model="productPrice" class="product-price" disabled></Rate>
          <p class="product-intro">
            打开窗户即可享受最清新的林间清风和最和煦的温暖阳光
          </p>
          <div class="product-buy">
            <DatePicker type="daterange" placement="bottom-end" placeholder="选择入住时间" style="width: 200px"></DatePicker>
            <Button type="error">预定</Button>
          </div>
        </div>
      </div>
      <Tabs type="card" class="product-more-info">
        <TabPane label="房间介绍">
          <div class="detail"></div>
        </TabPane>
        <TabPane label="用户评价">
          <div class="detail">
            <div class="comment" v-for="item in userComponent">
              <div class="user-info">
                <div class="user-pic"></div>
                <p class="user-name">{{item.user_name}}</p>
              </div>
              <div class="user-say">
                <p class="user-word">{{item.user_word}}</p>
                <Rate disabled v-model="item.user_rate"></Rate>
              </div>
            </div>
          </div>
        </TabPane>
        <TabPane label="入住须知">
          <div class="detail"></div>
        </TabPane>
      </Tabs>
    </div>
</template>

<script>
  export default {
    name: 'hotelMess',
    data () {
      return {
        productPrice: 4,
        userRate: 3.5,
        userComponent: [
          {
            user_name: '愤怒的小鸟',
            user_word: '这个房间真是又大又舒服！阳光也充足！下次一定再来啦！',
            user_rate: 3.5,
            user_info_pic: 'card1'
          },
          {
            user_name: '愤怒的企鹅',
            user_word: '楼上说得对！',
            user_rate: 5,
            user_info_pic: 'card1'
          },
          {
            user_name: '开心的小鸟',
            user_word: '真的是非常不错了！',
            user_rate: 3.5,
            user_info_pic: 'card1'
          }
        ]
      }
    }
  }
</script>

<style scoped>

  .product-mess {
    margin-top: 50px;
    margin-left: auto;
    margin-right: auto;
    width: 800px;
    display: flex;
    flex-direction: row;
    justify-content: center;
    border-bottom: solid 2px #dfdfdf;
    padding-bottom: 30px;
  }

  .left-product-pic {
    width: 300px;
    background-color: orange;
    height: 250px;
    margin-right: 20px;
  }

  .right-product-info {
    display: flex;
    flex-direction: column;
    width: 400px;
  }
  .product-name {
    font-size: 30px;
  }
  .product-price {
    font-size: 30px;
  }
  .price-number {
    color: red;
    font-size: 35px;
  }
  .product-intro {
    font-size: 25px;
  }
  .product-more-info {
    width: 800px;
    margin-left: auto;
    margin-right: auto;
    margin-top: 50px;
  }
  .detail {
    min-height: 300px;
    background-color: white;
  }
  .comment {
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    margin-left: 30px;
    padding-top: 20px;
    border-bottom: solid 1px #dfdfdf;
    width: 90%;
  }
  .user-pic {
    width: 50px;
    height: 50px;
    background-color: orange;
    margin-right: auto;
    margin-left: auto;
  }
  .user-info {
    margin-right: 50px;
  }
</style>



