var staticRenderFns = [function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"aticle"},[_c('div',{staticClass:"main-content"},[_c('div',{staticClass:"art-head"},[_c('div',{staticClass:"title"},[_vm._v("什么是农副产品")]),_vm._v(" "),_c('div',{staticClass:"time"},[_vm._v("2017-10-25")])]),_vm._v(" "),_c('div',{staticClass:"article-content"},[_c('div',{staticClass:"article-pic"}),_vm._v(" "),_c('pre',{staticClass:"article-word"},[_vm._v("        农业与农副产品\n        农业是指通过培育动植物生产食品及工业原料的产业。\n        农业属于第一产业，研究农业的科学是农学。 农业的劳动对象是有生命的动植物，获得的产品是动植物本身。\n        我们把利用动物植物等生物的生长发育规律，通过人工培育来获得产品的各部门，统称为农业。\n        农副产品是指种植业、养殖业、林业、牧业、水产业等产业进行初级加工形成的产品就是农副产品。[1]\n        农副产品分类\n        植物类\n        植物类包括人工种植和天然生长的各种植物的初级产品。\n        ⑴ 粮食\n        粮食是指各种主食食科植物果实的总称。本货物的征税范围包括小麦、稻谷、玉米、高粱、谷子和其他杂粮（如：大麦、燕麦等），\n        以及经碾磨、脱壳等工艺加工后的粮食（如：面粉，米，玉米面、渣等）。\n        切面、饺子皮、馄饨皮、面皮、米粉等粮食复制品，也属于本货物的征税范围。\n        以粮食为原料加工的速冻食品、方便面、副食品和各种熟食品，不属于本货物的征税范围。\n        ⑵ 蔬菜\n        蔬菜是指可作副食的草本、木本植物的总称。本货物的征税范围包括各种蔬菜、菌类植物和少数可作副食的木本植物。\n        经晾晒、冷藏、冷冻、包装、脱水等工序加工的蔬菜，腌菜、咸菜、酱菜和盐渍蔬菜等，也属于本货物的征税范围。\n        各种蔬菜罐头（罐头是指以金属罐、玻璃瓶和其他材料包装，经排气密封的各种食品）。\n        ⑶ 烟叶\n        烟叶是指各种烟草的叶片和经过简单加工的叶片。本货物的征税范围包括晒烟叶、晾烟叶和初烤烟叶。\n        a．晒烟叶。是指利用太阳能露天晒制的烟叶。\n        b．晾烟叶。是指在晾房内自然干燥的烟叶。\n        c．初考烟叶。是指烟草种植者直接烤制的烟叶。不包括专业复烤厂烤制的复烤烟叶。\n        ⑷ 茶叶\n        茶叶是指从茶树上采摘下来的鲜叶和嫩芽（即茶青），以及经吹干、揉拌、发酵、烘干等工序初制的茶。\n        本货物的征税范围包括各种毛茶（如红毛茶、绿毛茶、乌龙毛茶、白毛茶、黑毛茶等）。\n        精制茶、边销茶及掺对各种药物的茶和茶饮料。\n        ⑸ 园艺植物\n        园艺植物是指可供食用的果实，如水果、果干（如荔枝干、桂圆干、葡萄干等）、干果、果仁、果用瓜（如甜瓜、西瓜、哈密瓜等），\n        以及胡椒、花椒、大料、咖啡豆等。\n        经冷冻、冷藏、包装等工序加工的园艺植物，也属于本货物的征税范围。\n        各种水果罐头，果脯，蜜饯，炒制的果仁、坚果，碾磨后的园艺植物（如胡椒粉、花椒粉等。\n        ⑹ 药用植物\n        药用植物是指用作中药原药的各种植物的根、茎、皮、叶、花、果实等。\n        利用上述药用植物加工制成的片、丝、块、段等中药饮片，也属于本货物的征税范围。\n        ⑺ 油料植物\n        油料植物是指主要用作榨取油脂的各种植物的根、茎、叶、果实、花或者胚芽组织等初级产品，\n        如菜子（包括芥菜子）、花生、大豆、葵花子、蓖麻子、芝麻子、胡麻子、茶子、桐子、橄榄仁、棕榈仁、棉籽等。\n        提取芳香油的芳香油料植物。\n        ⑻ 纤维植物\n        纤维植物是指利用其纤维作纺织、造纸原料或者绳索的植物，如棉（包括籽棉、皮棉、絮棉）、\n        大麻、黄麻、槿麻、苎麻、苘麻、亚麻、罗布麻、蕉麻、剑麻等。\n        棉短绒和麻纤维经脱胶后的精干（洗）麻。\n        ⑼ 糖料植物\n        糖料植物是指主要用作制糖的各种植物，如甘蔗、甜菜等。\n        ⑽ 林业产品\n        林业产品是指乔木、灌木和竹类植物，以及天然树脂、天然橡胶。\n        a．原木。是指将砍伐倒的乔木去其枝芽、梢头或者皮的乔木、灌木，以及锯成一定长度的木段。\n        锯材不属于本货物的征税范围。\n        b．原竹。是指将砍倒的竹去其枝、梢或者叶的竹类植物，以及锯成一定长度的竹段。\n        c．天然树脂。是指木科植物的分泌物，包括生漆、树脂和树胶，如松脂、桃胶、樱胶、阿拉伯胶、古巴胶和天然橡胶（包括乳胶和干胶）等。\n        d．其他林业产品。是指除上述列举林业产品以外的其他各种林业产品，如竹笋、笋干、棕竹、棕榈衣、树枝、树叶、树皮、藤条等。\n        其他植物\n        其他植物是指除上述列举植物以外的其他各种人工种植和野生的植物，如树苗、花卉、植物种子、\n        植物叶子、草、麦秸、豆类、薯类、藻类植物等。\n        干花、干草、薯干、干制的藻类植物，农业产品的下脚料等，也属于本货物的征税范围。\n      ")])])])])}]
